
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'



const baseList = [{
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw1k2wj20p00goq7n.jpg',
title: '送你一辆车'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/5.jpg', // 404
title: '送你一次旅行',
fallbackImg: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw50iwj20ff0aaaci.jpg'
}]

export default {
directives: {
TransferDom,
},
components: {
Group,
Cell,
XAddress,     // 地址
Datetime,     // 日期插件
Swiper,       // 轮播图
Previewer,    // 图片预览
TransferDom,  // 过度效果
},
data () {
return {




title: '标题',
showAddress: false,
msg: 'Hello World!',
value: [],
noonValue: '2018-04-13 PM',
addressData: ChinaAddressV4Data,
demo01_list: baseList,
demo01_index: 0,
preimgList: [
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
w: 800,
h: 400
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
w: 1200,
h: 900
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwwcynw2j20p00b4js9.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwwcynw2j20p00b4js9.jpg'
}
],
preimgoptions: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
},
logIndexChange (arg) {
console.log(arg)
}
}
},
methods: {
tobbb (e){
this.$router.push('/bbb');
console.log(e);
},
showLoading () {
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
this.$vux.loading.hide()
}, 2000)
},
doShowAddress () {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
onShadowChange (ids, names) {
console.log(ids, names)
},
changeData () {
this.value2 = ['430000', '430400', '430407']
},
changeDataByLabels () {
this.value2 = ['广东省', '广州市', '天河区']
},
changeDataByLabels2 () {
this.value2 = ['广东省', '中山市', '--']
},
getName (value) {
return value2name(value, ChinaAddressV4Data)
},
logHide (str) {
console.log('on-hide', str)
},
logShow (str) {
console.log('on-show')
},
demo01_onIndexChange (index) {
this.demo01_index = index
},
show (index) {
this.$refs.previewer.show(index)
}
},
created () {
console.log('vue: ',this);
}
}



